body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #eaeded;
}

.App {
  text-align: center;
}

.App-main {
  display: flex;
  padding: 14px 18px 0 18px;
  align-items: flex-start;
}

.App-header {
  background-color: #0a2546;
  color: white;
  display: flex;
  justify-content: center;
}

.CartItem {
  display: flex;
  border-bottom: 1px solid #ddd;
  padding-bottom: 12px;
  padding-top: 12px;
}

.CartItem-image {
  width: 180px;
  height: 180px;
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 16px;
}

.CartItem-image img {
  object-fit: contain;
  width: 100%;
}

.CartItem-info {
  flex-grow: 1;
}

.info-title h2 {
  margin: 0;
  font-size: 18px;
  color: #007185;
}

.info-stock {
  margin-top: 4px;
  color: #007600;
}

.item-actions {
  display: flex;
  align-items: center;
  margin-top: 4px;
}

.item-quantity select {
  background: #f0f2f2;
  padding: 8px;
  border-radius: 7px;
  box-shadow: 0px 2px 5px 0px rgb(15 17 17 / 15%);
}
.item-quantity select:focus {
  outline: none;
}

.item-actions-divider {
  color: #dddddd;
  font-weight: bold;
  margin: 0px 8px;
  height: 20px;
}

.item-delete {
  color: #007185;
}
.item-delete:hover {
  cursor: pointer;
  text-decoration: underline;
}

.CartItem-price {
  font-size: 18px;
  font-weight: 700;
  margin-left: 16px;
}

.CartItems {
  background-color: white;
  flex: 0.7 1;
  margin-right: 18px;
  text-align: left;
  padding: 20px;
}

.CartTotal {
  background-color: white;
  flex: 0.3 1;
  text-align: left;
  padding: 20px;
  min-width: 300px;

}

.CartTotal button {
  background-color: #f0c14b;
  font-size: 16px;
  width: 100%;
  padding: 4px 8px;
  border-radius: 4px;
  border: 2px solid #a88734;
  cursor: pointer;
}

.CartTotal button:hover {
  background-color: #ddb347;
}

.CartTotal button:focus {
  outline: none;
}

.CartTotal-price {
  margin-left: 8px;
}

